.no-scroll {
  overflow: hidden;
}
.te-product-overlay {
  scroll-behavior: smooth;
  background: #b4b4b4;
  background-color: rgba(180, 180, 180, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 8000;
  overflow-y: scroll;
  min-width: 310px;
  padding: 10px;
}
.te-product-overlay .te-product-card-modal {
  -webkit-box-shadow: 5px 5px 5px -4px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 5px 5px 5px -4px rgba(0, 0, 0, 0.25);
  box-shadow: 5px 5px 5px -4px rgba(0, 0, 0, 0.25);
  z-index: 9000;
  max-width: 1350px;
  margin: 0 auto;
  position: relative;
  top: 10px;
  background: white;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  -webkit-backface-visibility: hidden;
  display: block;
}
.te-product-overlay .te-product-card-modal.display-block {
  display: block;
}
.te-product-overlay .te-product-card-modal.display-none {
  display: none;
}
.te-product-overlay .te-product-card-modal-inner {
  padding-top: 60px;
}
.te-product-overlay .te-product-card-modal-inner .te-tipser-product-wrapper {
  margin: 0;
  padding: 0 10px;
}
.te-product-overlay .te-product-card-modal-inner .te-tipser-product-wrapper .te-product-details .te-product-description .te-product-description-title {
  font-weight: 400;
  font-size: 12px;
}
.te-product-overlay .te-product-card-modal-inner .te-tipser-product-wrapper .te-product-details .te-product-description .te-product-description-title:hover {
  cursor: pointer;
}
.te-product-overlay .te-product-card-modal-inner .te-tipser-product-wrapper .te-product-details .te-product-description .te-product-description-content {
  font-size: 0.9em;
  background: -webkit-linear-gradient(#333, #eee);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.te-product-overlay .te-product-card-modal-inner .te-product-info-container {
  padding: 0;
  font-size: 1.2em;
}
.te-product-overlay .te-product-card-modal .te-product-header-container .te-product-card-modal-cart {
  background-color: white;
  z-index: 9999;
  position: fixed;
  display: flex;
  align-items: center;
  width: 94%;
  max-width: 1350px;
  padding: 10px 0;
  height: 35px;
  top: 0;
}
.te-product-overlay .te-product-card-modal .te-product-header-container .te-product-card-modal-cart .te-header {
  position: relative;
}
.te-product-overlay .te-product-card-modal .te-product-header-container .te-product-card-modal-cart .te-header .te-header-logo {
  margin-left: 20px;
}
.te-product-overlay .te-product-card-modal .te-product-header-container .te-product-card-modal-cart .te-header .te-header-logo img {
  max-width: 250px;
  max-height: 40px;
}
.te-product-overlay .te-product-card-modal .te-product-header-container .te-product-card-modal-cart .cart-icon {
  margin-left: auto;
}
.te-product-overlay .te-product-card-modal .te-product-header-container .te-product-card-modal-cart .te-button-cross {
  width: 20px;
  position: relative;
  margin-left: auto;
  margin-right: 20px;
  top: unset;
  right: unset;
  color: #4f5058;
}
.te-product-overlay .te-similar .te-similar-products {
  color: #444444;
}
.te-product-overlay .te-similar .te-similar-products .te-similar-products-container {
  padding: 0 10px;
}
.te-product-overlay .te-similar .te-similar-products .te-similar-products-container .te-similar-products-title {
  font-weight: 500;
  font-size: 1em;
  margin: 15px 30px;
  color: #4f5058;
}
.te-product-overlay .te-similar .te-similar-products .te-similar-products-container .te-similar-product {
