.te-product-overlay .te-similar .te-similar-products .te-similar-products-container .te-similar-product .te-similar-image img {
  max-width: 100%;
  max-height: 100%;
  margin: 0 auto;
}
.te-product-overlay .te-similar .te-similar-products .te-similar-products-container .slick-slider .slick-next,
.te-product-overlay .te-similar .te-similar-products .te-similar-products-container .slick-slider .slick-prev {
