}
.te-product-overlay .te-product.te-product-full .te-product-image .te-product-thumbnails {
  margin: 0;
  width: 100px;
}
.te-product-overlay .te-product.te-product-full .te-product-image .te-product-thumbnails .slick-list .te-product-thumb {
  width: 100px;
}
.te-product-overlay .te-product.te-product-full .te-product-image .te-product-thumbnails .slick-list .te-product-thumb .te-img {
  height: 100px;
}
.te-product-overlay .te-product.te-product-full .te-product-image .te-product-thumbnails .slick-list .te-product-thumb .te-img img {
  height: 100px;
}
.te-product-overlay .te-product.te-product-full .te-product-image .iiz {
  display: flex;
  align-items: flex-start;
  min-height: 600px;
  max-height: 700px;
  height: unset;
}
.te-style-with .te-similar-products {
  padding: 0 10px;
  color: #444444;
}
.te-style-with .te-similar-products .te-similar-products-container {
  margin-bottom: 20px;
}
.te-style-with .te-similar-products .te-similar-products-container .te-similar-products-title {
  font-weight: 500;
  font-size: 1em;
  margin: 15px 30px;
  color: #4f5058;
}
.te-style-with .te-similar-products .te-similar-products-container .te-similar-product {
  margin: 10px;
}
.te-style-with .te-similar-products .te-similar-products-container .te-similar-product .te-similar-image img {
  max-height: 100%;
}
.te-style-with .te-similar-products .te-similar-products-container .te-similar-product .te-similar-name {
  color: #a3a0a0;
  font-weight: 500;
  font-size: 0.9em;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.te-style-with .te-similar-products .te-similar-products-container .te-similar-product .te-similar-brand {
  display: none;
}
.te-style-with .te-similar-products .te-similar-products-container .te-similar-product .te-similar-price-no-sales {
