}
.te-style-with .te-similar-products .te-similar-products-container .slick-slider .slick-list .te-similar-product .te-similar-image {
  display: flex;
  align-items: center;
  justify-content: center;
}
.te-style-with .te-similar-products .te-similar-products-container .slick-slider .slick-list .te-similar-product .te-similar-image img {
  max-width: 100%;
  margin: 0 auto;
}
.te-style-with .te-similar-products .te-similar-products-container .slick-slider .slick-next,
.te-style-with .te-similar-products .te-similar-products-container .slick-slider .slick-prev {
  opacity: 1;
}
.te-style-with .te-similar-products .te-similar-products-container .slick-slider .slick-next svg,
.te-style-with .te-similar-products .te-similar-products-container .slick-slider .slick-prev svg {
  width: 50px;
  height: 50px;
}
.te-style-with .te-similar-products-container.te-xxsmall .te-similar-products-title {
  font-size: 0.88em;
}
.te-style-with .te-similar-products-container.te-xxsmall .te-similar-image {
