.te-style-with .te-similar-products-container.te-xsmall .te-similar-products-title {
  font-size: 0.88em;
}
.te-style-with .te-similar-products-container.te-xsmall .te-similar-image {
  height: 280px;
}
.te-style-with .te-similar-products-container.te-small .te-similar-image {
  height: 200px;
}
.te-style-with .te-similar-products-container.te-medium .te-similar-image {
  height: 200px;
}
.te-style-with .te-similar-products-container.te-large .te-similar-image {
  height: 240px;
}
.te-style-with .te-similar-products-container.te-xlarge .te-similar-image {
  height: 270px;
}
@-webkit-keyframes scaleout {
  0% {
    -webkit-transform: scale(0);
  }
  100% {
    -webkit-transform: scale(1);
    opacity: 0;
  }
}
@keyframes scaleout {
  0% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  100% {
    transform: scale(1);
    -webkit-transform: scale(1);
    opacity: 0;
  }
}
