.te-cart-summary-container {
  margin: 0 auto;
  padding: 10px 10px 20px 10px;
  background-color: #efefef;
  width: 420px;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.te-cart-summary-container .te-cart-summary-inner {
  width: 90%;
  margin: 0 auto;
}
.te-cart-summary-container .te-cart-summary-inner .te-cart-summary-title {
  font-size: 16px;
  color: #4f5058;
  margin: 5px 0 10px;
}
.te-cart-summary-container .te-cart-summary-inner .te-cart-summary {
  background-color: #fff;
  padding: 20px 15px;
  border-radius: 12px;
  font-size: 12px;
  color: #303248;
}
.te-cart-summary-container .te-cart-summary-inner .te-cart-summary .te-products-order {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
}
.te-cart-summary-container .te-cart-summary-inner .te-cart-summary .te-discount {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5px;
}
.te-cart-summary-container .te-cart-summary-inner .te-cart-summary .te-discount .discount-value {
  color: #ec877b;
}
.te-cart-summary-container .te-cart-summary-inner .te-cart-summary .te-shipping-cost {
  display: flex;
  justify-content: space-between;
  padding-bottom: 15px;
  border-bottom: 1px solid lightgray;
}
.te-cart-summary-container .te-cart-summary-inner .te-cart-summary .te-total-price {
  display: flex;
  justify-content: space-between;
  margin-top: 15px;
}
.te-cart-summary-container .te-cart-summary-inner .te-cart-summary .te-total-price .total-title {
  font-weight: 500;
}
.te-cart-summary-container .te-cart-summary-inner .te-cart-summary .te-total-price .total-value {
  font-weight: 500;
}
.te-cart-summary-container .te-cart-summary-inner .te-cart-summary .te-vat-amounts {
  display: flex;
  justify-content: space-between;
  margin-top: 5px;
}
