.te-product-modal-container {
  max-width: 1350px;
  width: 100%;
  position: relative;
  margin: 0 auto;
}
.te-product-modal-container .te-product-image {
  box-sizing: border-box;
  position: relative;
  margin: 0;
  margin-bottom: 30px;
  display: -webkit-box;
  display: flex;
  flex-direction: column-reverse;
  width: 100%;
  margin: 0 auto;
  align-items: center;
}
.te-product-modal-container .te-product-image-wrapper {
  min-height: 300px;
  max-height: 300px;
  width: 100%;
  display: flex;
}
.te-product-modal-container .te-product-image-wrapper .te-img {
  max-height: 300px;
  margin: 0 auto;
}
.te-product-modal-container .te-product-image-wrapper img {
  max-width: 100%;
  max-height: 100%;
}
.te-product-modal-container .te-product-thumbnails {
  display: block;
  position: relative;
  overflow: visible;
  width: 100%;
}
.te-product-modal-container .te-product-thumbnails .te-product-thumb {
  cursor: pointer;
  min-width: 50px;
  outline: none;
  position: relative;
  text-align: center;
}
.te-product-modal-container .te-product-thumbnails .te-product-thumb .te-img {
  max-width: 100%;
  max-height: 100%;
  margin: 0 auto;
  min-height: 80px;
  height: 88px;
}
.te-product-modal-container .te-product-thumbnails .te-product-thumb .te-img img {
  max-width: 100%;
  max-height: 100%;
  margin: 0 auto;
  min-height: 80px;
}
.te-product-modal-container .te-product-details {
  word-break: break-word;
}
.te-product-modal-container .te-product-details .warning-message {
  color: #ec877b;
  padding-bottom: 10px;
}
.te-product-modal-container .te-product-details .te-product-basic-info {
  font-weight: 500;
  text-align: left;
  display: -webkit-box;
  display: flex;
  flex-direction: column;
}
.te-product-modal-container .te-product-details .te-product-basic-info .te-product-name {
  font-size: 1.25em;
  line-height: 28px;
  font-weight: 500;
  text-transform: uppercase;
  color: #4f5058;
}
.te-product-modal-container .te-product-details .te-product-basic-info .te-product-brand {
  margin-bottom: 3px;
  font-size: 1em;
  text-transform: uppercase;
  font-weight: 500;
  letter-spacing: 2px;
  color: #a3a0a0;
}
.te-product-modal-container .te-product-details .te-product-basic-info .te-product-price-container {
  margin-top: 4px;
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
}
.te-product-modal-container .te-product-details .te-product-basic-info .te-product-price-container .te-product-price-main {
  grid-area: priceMain;
}
.te-product-modal-container .te-product-details .te-product-basic-info .te-product-price-container .te-product-price-main .te-product-sales-price {
  color: #ec877b;
  margin-left: 15px;
  letter-spacing: 0;
  font-size: 1.25em;
}
.te-product-modal-container .te-product-details .te-product-basic-info .te-product-price-container .te-product-price-main .te-product-regular-price {
  color: #4f5058;
  text-decoration: line-through;
  font-size: 1.25em;
}
.te-product-modal-container .te-product-details .te-product-basic-info .te-product-price-container .te-product-price-vat-info {
  grid-area: vatInfo;
  color: #a3a0a0;
  font-size: 0.75em;
  font-weight: 100;
  margin-top: 0.3em;
  margin-left: 5px;
  text-indent: 2px;
}
.te-product-modal-container .te-product-details .te-product-basic-info .te-product-price-container .te-product-discount-percentage {
  margin-top: 5px;
  font-size: 1.25em;
  grid-area: discPercentage;
  color: #ec877b;
}
.te-product-modal-container .te-product-details .te-button {
  cursor: pointer;
  margin: 0;
  width: 100%;
  font-size: 0.8em;
}
.te-product-modal-container .te-product-details .te-button.disabled {
  cursor: not-allowed;
}
.te-product-modal-container .te-product-details .te-button.disabled .te-button-body {
  background-color: #dddddd;
}
.te-product-modal-container .te-product-details .te-button.disabled .te-button-text {
  color: #525860;
}
.te-product-modal-container .te-product-details .te-product-info-container .te-product-usp {
  list-style-type: none;
  padding: 0;
  display: block;
}
.te-product-modal-container .te-product-details .te-product-info-container .te-product-usp-item {
  display: -webkit-box;
  display: flex;
  letter-spacing: 0.08em;
  line-height: 1.6;
}
.te-product-modal-container .te-product-details .te-product-info-container .te-product-usp-item .te-product-icon {
  margin-right: 5px;
}
.te-product-modal-container .te-product-details .te-product-info-container .te-product-usp-item .te-product-icon svg {
  width: 20px;
}
.te-product-modal-container .te-product-details .te-product-info-container .te-product-usp-item .te-product-info {
  display: -webkit-box;
  display: flex;
  font-size: 0.8em;
  letter-spacing: 0;
  color: #333;
}
.te-product-modal-container .te-product-details .te-product-info-container .te-product-usp-item .te-product-info-label {
  margin-right: 5px;
  color: #303248;
  font-size: 14px;
}
.te-product-modal-container .te-product-details .te-product-info-container .te-product-usp-item .te-product-info-label:first-letter {
  text-transform: capitalize;
}
.te-product-modal-container .te-product-details .te-product-description {
  padding-bottom: 10px;
}
@media (min-width: 768px) {
  .te-product-modal-container .te-product-details .te-product-description {
    padding-bottom: 17px;
  }
}
.te-product-modal-container .te-product-details .te-product-description-title {
  color: #303248;
  text-transform: capitalize;
  text-align: left;
  display: block;
  font-size: 12px;
  padding: 10px 10px 10px 0;
  position: relative;
  font-weight: bold;
  text-decoration: underline;
  margin-top: 20px;
}
.te-product-modal-container .te-product-details .te-product-description-title svg {
  width: 10px;
}
.te-product-modal-container .te-product-details .te-product-description-content {
  font-size: 0.8em;
  letter-spacing: 0;
  text-align: left;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
  overflow: scroll;
}
.te-product-modal-container .te-product-details .te-product-description-content.active {
  max-height: 200px;
  margin: 0;
}
.te-product-modal-container .te-product-details .te-product-description-content.more {
  -webkit-transition: max-height 0.2s ease-in;
  transition: max-height 0.2s ease-in;
  max-height: 1000px;
}
.te-product-modal-container .te-product-details .te-product-description-content p:first-child {
  font-size: inherit;
  margin: 0;
}
.te-product-modal-container .te-product-details .te-product-description-content p {
  line-height: 1.7;
  margin: 0;
  font-size: inherit;
}
.te-product-modal-container .te-product-details .te-product-description-more {
  margin: 5px 0;
  display: block;
  text-align: left;
  width: 50%;
  font-size: 12px;
  width: 90px;
  text-decoration: underline;
  font-weight: 500;
  cursor: pointer;
  color: #303248;
}
.te-product-modal-container .te-product-details .te-dropdown-container .te-dropdown-select {
  border: 1px solid #e7e7e7;
  text-align-last: left;
}
.te-product-modal-container .te-product-details .te-dropdown-container .te-dropdown-select .modal-select {
  color: #4f5058;
}
.te-product-modal-container .te-product-details .te-dropdown-container .te-dropdown-select .modal-select option {
  text-align-last: left;
}
.te-product-modal-container .te-product-details .te-dropdown-container svg {
  color: #4f5058;
}
.te-product-modal-container .slick-slide {
  display: inline-block;
  text-align: center;
}
.te-product-modal-container .icon-wrapper {
  margin-right: 8px;
}
.te-product-modal-container .icon-wrapper svg {
  width: 17px;
  margin-top: 3px;
}
.te-product-modal-container .te-product.te-single-product .iiz-index {
  font-weight: 500;
  position: absolute;
  z-index: 99;
  top: 6%;
  left: 10%;
  text-shadow: 1px 1px #ffffff;
}
.te-product.te-single-product__column {
  width: 80%;
  margin: 0 auto;
}
.te-product.te-single-product__column .te-product-image {
  flex-direction: row;
  justify-content: center;
  align-items: unset;
}
.te-product.te-single-product__column .te-product-image .te-product-thumbnails {
  width: 90px;
}
.te-product.te-single-product__column .te-product-details {
  width: 100%;
  margin: 30px auto 0 auto;
}
.te-product-full {
  margin: 30px auto;
  display: -webkit-box;
  display: flex;
  flex-direction: row;
  max-width: 100%;
  width: 100%;
}
.te-product-full .te-product-image {
  flex-direction: row;
  height: 100%;
  align-items: unset;
}
.te-product-full .te-product-image .iiz {
  margin: 0 25px;
}
.te-product-full .te-product-thumbnails {
  width: 90px;
}
.te-product-full .te-product-details {
  margin-left: 10px;
  width: 80%;
}
.te-single-product__column .te-product-image-wrapper,
.te-product-full .te-product-image-wrapper {
  max-height: 500px;
}
.te-single-product__column .te-product-image-wrapper .te-img,
.te-product-full .te-product-image-wrapper .te-img {
  max-height: 100%;
  min-height: 100%;
}
.te-single-product__column .te-product-thumbnails .te-product-thumb,
.te-product-full .te-product-thumbnails .te-product-thumb {
  min-width: 90px;
}
.te-single-product__column .slick-prev,
.te-product-full .slick-prev {
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  top: 0;
  left: 50%;
}
.te-single-product__column .slick-next,
.te-product-full .slick-next {
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  left: 50%;
  top: 100%;
}
.te-product-full-size {
  position: absolute;
  top: 0;
  left: 0;
  width: 150%;
  z-index: 99999;
  display: none;
}
.te-product-modal-container .te-product.te-single-product .te-product-details {
  padding: 0 10px;
}
.te-product-modal-container .te-product.te-single-product .te-product-details .te-product-basic-info {
  display: grid;
  grid-template-areas: 'productBrand productPrice' 'productName productPrice';
  margin-bottom: 20px;
}
.te-product-modal-container .te-product.te-single-product .te-product-details .te-product-basic-info .te-product-name {
  grid-area: productName;
}
.te-product-modal-container .te-product.te-single-product .te-product-details .te-product-basic-info .te-product-brand {
  grid-area: productBrand;
  align-self: start;
}
.te-product-modal-container .te-product.te-single-product .te-product-details .te-product-basic-info .te-product-price-container {
  grid-area: productPrice;
  align-self: start;
  justify-self: end;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 0;
}
.te-product-modal-container .te-product.te-single-product .te-product-details .te-product-basic-info .te-product-price-container .te-product-discount-percentage {
  display: none;
}
.te-product-modal-container .te-product.te-single-product .te-product-details .te-product-basic-info .te-product-price-container .te-product-price-main .te-product-sales-price {
  margin-left: 0;
}
.te-product-modal-container .te-product.te-single-product_column {
  display: grid;
  grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
}
.te-product-modal-container .te-product.te-single-product_column .te-product-image {
  align-self: start;
}
.te-product-modal-container .te-product.te-single-product_column .te-product-image .slick-track {
  display: flex;
  align-items: center;
}
.te-product-modal-container .te-product.te-single-product_column .te-product-image .slick-track .te-product-thumb .te-img {
  height: unset;
}
.te-product-modal-container .te-product.te-single-product_column .te-product-details {
  padding-left: 15px;
}
.te-product-modal-container .te-product.te-single-product_column .te-product-details .te-product-basic-info :nth-child(1) {
  order: 2;
}
.te-product-modal-container .te-product.te-single-product_column .te-product-details .te-product-basic-info :nth-child(2) {
  order: 1;
}
.te-product-modal-container .te-product.te-single-product_column .te-product-details .te-product-basic-info :nth-child(3) {
  order: 3;
}
.te-product-modal-container .te-product.te-single-product_column .te-product-details .te-product-basic-info .te-product-price-container .te-product-price-main {
  display: flex;
  align-items: center;
}
.te-product-modal-container .te-product.te-single-product_column .te-product-details .te-product-basic-info .te-product-price-container .te-product-discount-percentage {
  align-self: flex-end;
}
.te-product-modal-container .te-product.te-single-product_column .te-product-details .te-dropdown-container {
  width: 50%;
  margin: 30px 0;
}
.te-product-modal-container .te-product.te-single-product_column .te-product-details .te-color-relations-wrapper {
  margin: 100px 0 20px 0;
}
.te-product-modal-container .te-product.te-single-product_column .slick-slide {
  padding: 0 10px;
}
.te-product-modal-container .te-product.te-product-full {
  display: grid;
  grid-template-columns: 1.1fr 0.9fr;
}
.te-product-modal-container .te-product.te-product-full .te-product-image {
  max-height: 600px;
}
.te-product-modal-container .te-product.te-product-full .te-product-image .iiz {
  display: flex;
  align-items: flex-start;
  max-height: 600px;
  height: unset;
}
.te-product-modal-container .te-product.te-product-full .te-product-details .te-product-basic-info :nth-child(1) {
  order: 2;
}
.te-product-modal-container .te-product.te-product-full .te-product-details .te-product-basic-info :nth-child(2) {
  order: 1;
}
.te-product-modal-container .te-product.te-product-full .te-product-details .te-product-basic-info :nth-child(3) {
  order: 3;
}
.te-product-modal-container .te-product.te-product-full .te-product-details .te-product-basic-info .te-product-price-container .te-product-price-main {
  display: flex;
  align-items: center;
}
.te-product-modal-container .te-product.te-product-full .te-product-details .te-dropdown-container {
  width: 50%;
  margin: 30px 0;
}
.te-product-modal-container .te-product.te-product-full .te-product-details .te-product-description-content {
  overflow: hidden;
}
.te-product-modal-container .te-product.te-product-full .te-product-details .te-product-description-content .te-product-description-content {
  padding-right: 10px;
}
.te-product-modal-container .te-product.te-product-full .te-product-details .te-product-price-container,
.te-product-modal-container .te-product.te-single-product_column .te-product-details .te-product-price-container {
  display: grid;
  grid-template-areas: 'priceMain vatInfo' 'discPercentage discPercentage';
}
