.te-product-list-container {
  margin: 0 auto;
  width: 400px;
  height: auto;
}
.te-product-list-container .te-product-list {
  background-color: #ffffff;
}
.te-product-list-container .te-product-list .te-product-merchant-header {
  border-bottom: 1px solid #ebebeb;
  font-size: 0.9em;
  padding: 12px;
}
.te-product-list-container .te-product-list .te-product-merchant-header .te-merchant {
  font-weight: 500;
  color: #444444;
}
.te-product-list-container .te-product-list .te-product-merchant-header .te-merchant-name {
  color: #2c2c2c;
  font-weight: 600;
  margin-left: 5px;
}
.te-product-list-container .te-product-list .te-single-product {
  padding: 0 20px 20px 20px;
}
.te-product-list-container .te-product-list .te-single-product .te-product-info-wrapper {
  border-top: 1px solid #ebebeb;
  padding-top: 20px;
  display: flex;
  align-items: center;
}
.te-product-list-container .te-product-list .te-single-product .te-product-info-wrapper .te-image-wrapper img {
  max-width: 80px;
  max-height: 80px;
}
.te-product-list-container .te-product-list .te-single-product .te-product-info-wrapper .te-product-info {
  margin-left: 20px;
}
.te-product-list-container .te-product-list .te-single-product .te-product-info-wrapper .te-product-info .te-product-name {
  font-weight: 500;
}
.te-product-list-container .te-product-list .te-single-product .te-product-info-wrapper .te-product-info .te-product-attribute {
  font-size: 0.8em;
}
.te-product-list-container .te-product-list .te-single-product .te-product-info-wrapper .te-product-info .te-delivery-time {
  font-size: 0.8em;
  color: #a6aaa9;
}
.te-product-list-container .te-product-list .te-single-product .te-product-quantity-wrapper {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 20px 0 0 20px;
}
.te-product-list-container .te-product-list .te-single-product .te-product-quantity-wrapper .te-remove {
  display: flex;
}
.te-product-list-container .te-product-list .te-single-product .te-product-quantity-wrapper .te-remove svg {
  height: 20px;
}
.te-product-list-container .te-product-list .te-single-product .te-product-quantity-wrapper .te-remove:hover {
  cursor: pointer;
}
.te-product-list-container .te-product-list .te-single-product .te-product-quantity-wrapper .te-quantity {
  display: flex;
}
.te-product-list-container .te-product-list .te-single-product .te-product-quantity-wrapper .te-quantity .te-quantity-text {
  margin: 0 10px;
}
.te-product-list-container .te-product-list .te-single-product .te-product-quantity-wrapper .te-quantity svg {
  width: 20px;
}
.te-product-list-container .te-product-list .te-single-product .te-product-quantity-wrapper .te-product-price {
  display: flex;
  font-weight: 600;
  color: #444444;
}
.te-product-list-container .te-product-list .te-single-product .te-product-quantity-wrapper .te-product-price .te-product-price-sales .te-price-sales {
  color: #cd5550;
}
.te-product-list-container .te-product-list .te-single-product .te-product-quantity-wrapper .te-product-price .te-product-price-sales .te-price-regular {
  margin-left: 7px;
  text-decoration: line-through;
}
