.te-modal-description-container .te-modal-description {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f6f6f6;
  width: 100%;
  margin: 0 auto;
}
@media (min-width: 500px) {
  .te-modal-description-container .te-modal-description {
    width: 95%;
  }
}
.te-modal-description-container .te-modal-description .te-modal-description-title {
  margin: 40px 0 30px 0;
  font-weight: 500;
  font-size: 16px;
  color: #4f5058;
}
.te-modal-description-container .te-modal-description .te-modal-description-content {
  padding: 10px 23px;
  word-break: break-word;
  margin: 0 auto;
  font-size: 14px;
  max-width: 900px;
}
.te-modal-description-container .te-modal-description .te-modal-description-content p {
  font-size: 14px;
}
