.te-footer-container {
  box-sizing: border-box;
  font-family: inherit;
  font-size: 14px;
  text-align: center;
  border-top: 1px solid #dcdee0;
  background-color: #f6f6f6;
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-bottom: 10px;
}
.te-footer-container .te-footer-contact {
  padding: 20px 25px;
  display: flex;
  color: #4f5058;
  flex-direction: column;
  align-items: flex-start;
}
.te-footer-container .te-footer-contact .te-footer-payment-title {
  font-weight: bold;
}
.te-footer-container .te-footer-contact .te-footer-payment-shopping {
  text-align: left;
  margin-top: 10px;
}
.te-footer-container .te-footer-contact .te-footer-payment-shopping .te-payment-infos {
  padding: 0;
  display: flex;
  flex-direction: column;
  font-size: 0.8em;
  line-height: 1.6em;
}
.te-footer-container .te-footer-contact .te-footer-payment-shopping .te-payment-infos .te-payment-info {
  display: flex;
  align-items: center;
  line-height: 2.5em;
}
.te-footer-container .te-footer-contact .te-footer-payment-shopping .te-payment-infos .te-payment-info svg {
  width: 20px;
  height: 20px;
  margin-right: 7px;
}
.te-footer-container .te-footer-contact .te-footer-payment-contact {
  text-align: right;
  display: flex;
  justify-content: center;
  flex-direction: column;
  margin-top: 10px;
}
.te-footer-container .te-footer-contact .te-footer-payment-contact .te-footer-payment-title {
  text-align: left;
}
.te-footer-container .te-footer-contact .te-footer-payment-contact .te-payment-infos {
  list-style-type: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  font-size: 0.8em;
  line-height: 1.6em;
}
.te-footer-container .te-footer-contact .te-footer-payment-contact .te-payment-infos .te-payment-info {
  display: flex;
  align-items: center;
  line-height: 2.5em;
}
.te-footer-container .te-footer-contact .te-footer-payment-contact .te-payment-infos .te-payment-info a {
  color: #262626;
  text-decoration: none;
}
.te-footer-container .te-footer-contact .te-footer-payment-contact .te-payment-infos .te-payment-info svg {
  width: 20px;
  height: 20px;
  margin-right: 7px;
  fill: #000;
}
.te-footer-container .te-footer-contact .te-footer-payment-contact .te-payment-infos .te-payment-info a:hover {
  color: #a3a0a0;
}
.te-footer-container .te-footer-contact .te-footer-payment-help {
  text-align: left;
  margin-top: 10px;
}
.te-footer-container .te-footer-contact .te-footer-payment-help .te-payment-infos {
  list-style-type: none;
  padding: 0;
}
.te-footer-container .te-footer-contact .te-footer-payment-help .te-payment-infos .te-payment-link {
  font-size: 0.8em;
  line-height: 2.4em;
}
.te-footer-container .te-footer-contact .te-footer-payment-help .te-payment-infos .te-payment-link a {
  color: #262626;
  text-decoration: none;
}
.te-footer-container .te-footer-contact .te-footer-payment-help .te-payment-infos .te-payment-link a:hover {
  color: #a3a0a0;
}
.te-footer-container a {
  border-bottom: 1px solid transparent;
  transition: border-color 400ms ease;
}
.te-footer-container .te-footer-payment-logos {
  display: flex;
  flex-direction: column;
  text-align: left;
}
@media (min-width: 768px) {
  .te-footer-container .te-footer-payment-logos {
    margin: 20px auto;
    text-align: center;
  }
}
.te-footer-container .te-footer-payment-logos .te-footer-payment-title {
  font-weight: bold;
  margin: 10px auto;
  font-size: 16px;
  letter-spacing: 0.5px;
}
.te-footer-container .te-footer-payment-logos .te-payment-logos {
  display: flex;
  justify-content: center;
  align-items: center;
}
.te-footer-container .te-footer-payment-logos .te-payment-logos :not(:first-child):not(:last-child) {
  margin: 0 10px;
}
.te-footer-container .te-footer-payment-logos .te-payment-logos .te-payment-logo {
  border-radius: 5px;
  width: 45px;
  height: 35px;
  padding: 10px 0;
  padding: 10px 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.te-footer-container .te-footer-payment-logos .te-payment-logos .te-payment-logo > svg {
  width: 100%;
  height: 100%;
}
.te-footer-container .te-footer-tipser-logo {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-end;
  margin: 5px 30px 40px 0;
  color: #4f5058;
}
.te-footer-container .te-footer-tipser-logo .logo-icon {
  margin-left: 7px;
}
.te-footer-container .te-footer-tipser-logo .logo-icon svg {
  width: 100px;
}
.te-footer-container .te-footer-terms .footer-terms {
  list-style-type: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 10px 30px 10px;
  padding: 0;
  font-size: 12px;
}
.te-footer-container .te-footer-terms .footer-terms .footer-term .footer-break {
  margin: 0 5px;
}
.te-footer-container .te-footer-terms .footer-terms .footer-term a {
  color: #262626;
  text-decoration: none;
}
.te-footer-container .te-footer-terms .footer-terms .footer-term a:hover {
  color: #a3a0a0;
}
.te-footer-container .te-footer.te-footer_row .te-footer-contact {
  flex-direction: row;
  justify-content: space-around;
}
