.no-scroll {
  overflow: hidden;
}
.te-product-overlay {
  scroll-behavior: smooth;
  background: #b4b4b4;
  background-color: rgba(180, 180, 180, 0.5);
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 8000;
  overflow-y: scroll;
  min-width: 310px;
  padding: 10px;
}
.te-product-overlay .te-product-card-modal {
  -webkit-box-shadow: 5px 5px 5px -4px rgba(0, 0, 0, 0.25);
  -moz-box-shadow: 5px 5px 5px -4px rgba(0, 0, 0, 0.25);
  box-shadow: 5px 5px 5px -4px rgba(0, 0, 0, 0.25);
  z-index: 9000;
  max-width: 1350px;
  margin: 0 auto;
  position: relative;
  top: 10px;
  background: white;
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  -webkit-backface-visibility: hidden;
  display: block;
}
.te-product-overlay .te-product-card-modal.display-block {
  display: block;
}
.te-product-overlay .te-product-card-modal.display-none {
  display: none;
}
